<template>
  <div class="gallery" ref="galleryContainer">
    <div v-if="showFirstImage && galleryItems && galleryItems.length > 0" class="gallery-item">
      <div class="image-container">
        <img :src="galleryItems[currentIndex].image" :alt="'Gallery Image ' + (currentIndex + 1)" class="gallery-image" />
        <div class="image-description">
          <p>{{ galleryItems[currentIndex].description }}</p>
          <button @click="showNextImage" v-if="showButton" class="see-more-button">{{ buttonText }}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GaleriaPacjentow',
  props: {
    items: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      currentIndex: 0,
      showFirstImage: true,
      showButton: true
    };
  },
  computed: {
    galleryItems() {
      return this.items;
    },
    buttonText() {
      return this.currentIndex === this.galleryItems.length - 1 ? 'Powrót do początku' : 'Zobacz dalej';
    }
  },
  methods: {
    showNextImage() {
      if (this.currentIndex < this.galleryItems.length - 1) {
        this.currentIndex++;
      } else {
        this.showFirstImage = true; // Jeśli doszliśmy do końca, wróć do pierwszego zdjęcia
        this.currentIndex = 0;
      }
      this.showButton = true; // Pokaż przycisk, ponieważ zawsze możemy kliknąć "Powrót do początku"
    }
  }
};
</script>

<style scoped>
.gallery {
  display: flex;
  justify-content: center;
}

.gallery-item {
  max-width: 800px; /* Dostosuj maksymalną szerokość galerii według potrzeb */
}

.image-container {
  display: flex;
  align-items: center;
}

.gallery-image {
  max-width: 800px;
  max-height: 600px; /* Ustal maksymalną wysokość obrazka */
  width: auto;
  height: auto;
  border-radius: 10px;
  margin-right: 20px; /* Dodatkowy margines między obrazkiem a opisem */
}

.image-description {
  flex: 1;
}

.see-more-button {
  background-color: #49c1a4;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.see-more-button:hover {
  background-color: #37a38a;
}
</style>
