import { createRouter, createWebHistory } from 'vue-router'; // Użyj odpowiednich elementów z vue-router

import Home from '@/views/Home.vue';
import ONas from '@/views/ONas.vue';
import Pacjenci from '@/views/Pacjenci.vue';
import Uslugi from '@/views/Uslugi.vue';
import Profilaktyka from '@/views/Profilaktyka.vue';
import Okulistyka from '@/views/Okulistyka.vue';
import Kardiologia from '@/views/Kardiologia.vue';
import USG from '@/views/USG.vue';
import Chirurgia from '@/views/Chirurgia.vue';
import Diagnostka from '@/views/Diagnostyka.vue';
import Dermatologia from '@/views/Dermatologia.vue';
import Stomatologia from '@/views/Stomatologia.vue';
import Kontakt from '@/views/Kontakt.vue';
import Zespol from '@/views/Zespol.vue';
import Cookies from '@/views/Cookies.vue';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/o-nas',
    name: 'ONas',
    component: ONas
  },
  {
    path: '/pacjenci',
    name: 'Pacjenci',
    component: Pacjenci
  },
  {
    path: '/uslugi',
    name: 'Uslugi',
    component: Uslugi
  },
  {
    path: '/zespol',
    name: 'Zespol',
    component: Zespol
  },
  {
    path: '/polityka-cookies',
    name: 'Cookies',
    component: Cookies
  },
  {
    path: '/kontakt',
    name: 'Kontakt',
    component: Kontakt
  },
  {
    path: '/profilaktyka-leczenie-ogolne',
    name: 'Profilaktyka',
    component: Profilaktyka
  },
  {
    path: '/okulistyka',
    name: 'Okulistyka',
    component: Okulistyka
  },
  {
    path: '/kardiologia',
    name: 'Kardiologia',
    component: Kardiologia
  },
  {
    path: '/usg',
    name: 'USG',
    component: USG
  },
  {
    path: '/diagnostyka',
    name: 'Diagnostyka',
    component: Diagnostka
  },
  {
    path: '/chirurgia',
    name: 'Chirurgia',
    component: Chirurgia
  },
  {
    path: '/Dermatologia',
    name: 'Dermatologia',
    component: Dermatologia
  },
  {
    path: '/stomatologia',
    name: 'Stomatologia',
    component: Stomatologia
  }
];

const router = createRouter({
  history: createWebHistory('/petcare/'),
  routes
});

router.beforeEach((to, from, next) => {
  const viewportMeta = document.querySelector('meta[name="viewport"]');
  if (viewportMeta) {
    viewportMeta.content = 'width=device-width, initial-scale=1.0';
  }
  next();
});

export default router;