<template>
    <section class="gallery">
      <div class="image-container" v-for="(photo, index) in photos" :key="index">
        <img :src="photo.src" :alt="photo.title" class="gallery-image" @click="openLightbox(index)">
        <div class="image-overlay">
          <h3>{{ photo.title }}</h3>
        </div>
      </div>
      <div v-if="showLightbox" class="lightbox-overlay" @click="closeLightbox">
      <div class="lightbox" :style="{ width: lightboxWidth + 'px', height: lightboxHeight + 'px' }">
        <img :src="selectedPhoto.src" :alt="selectedPhoto.title" class="lightbox-image">
        <button @click="closeLightbox" class="close-button">Zamknij</button>
      </div>
    </div>
    </section>
  </template>
  
  <script>
  export default {
    name: 'Gallery',
    data() {
      return {
        photos: [
        { src: 'img/galeria/4/9.JPG', title: 'Opis obrazu 2' },
        { src: 'img/galeria/4/8.JPG', title: 'Opis obrazu 3' },
        { src: 'img/galeria/4/3.JPG', title: 'Opis obrazu 1' },
        { src: 'img/galeria/3/3.JPG', title: 'Opis obrazu 2' },
        { src: 'img/galeria/2/3.JPG', title: 'Opis obrazu 3' },
        { src: 'img/galeria/2/1.JPG', title: 'Opis obrazu 5' },
        { src: 'img/galeria/1/3.JPG', title: 'Opis obrazu 1' },
        { src: 'img/galeria/1/4.JPG', title: 'Opis obrazu 2' },
        ],
        showLightbox: false,
        selectedPhoto: null,
        lightboxWidth: 0,
        lightboxHeight: 0,
        imageHeight: 0,
        imageWidth: 0,
      };
    },
    methods: {
      openLightbox(index) {
        this.selectedPhoto = this.photos[index];
        this.calculateLightboxSize();
        this.showLightbox = true;
      },
      closeLightbox() {
        this.showLightbox = false;
      },

      calculateLightboxSize() {
        const screenWidth = window.innerWidth;
        const screenHeight = window.innerHeight;
        const image = new Image();
        image.src = this.selectedPhoto.src;
        image.onload = () => {
          const imageWidth = image.naturalWidth;
          const imageHeight = image.naturalHeight;
          if (imageHeight > screenHeight) {
            const ratio = screenHeight / imageHeight;
            this.lightboxHeight = screenHeight * 0.8;
            this.lightboxWidth = imageWidth * ratio;
          } else {
            this.lightboxHeight = imageHeight;
            this.lightboxWidth = imageWidth;
          }
      }
    }
  }
  };
  </script>
  
  <style scoped>

.lightbox-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; /* Wartość z-index, aby umieścić lightbox na wierzchu */
}

.lightbox {
  max-width: 100%;
  max-height: 100%;
  overflow: hidden;
}

.lightbox-image {
  width: 100%;
  height: auto;
  display: block;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
}

  .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 10px;
    padding: 20px;
  }
  
  .image-container {
    position: relative;
    overflow: hidden;
  }
  
  .gallery-image {
    width: 100%;
    height: auto;
    object-fit: cover; /* Zachowaj proporcje obrazu */
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  
  .image-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    padding: 10px;
    text-align: center;
    transform: translateY(100%);
    transition: transform 0.3s ease;
  }
  
  .image-container:hover .image-overlay {
    transform: translateY(0);
  }
  
  @media screen and (max-width: 768px) {
    .gallery {
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
  }
  </style>
  