import { createApp } from 'vue'; // Upewnij się, że importujesz createApp z vue
import App from './App.vue';
import router from './router/router';
import BootstrapVueNext from 'bootstrap-vue-next'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue-next/dist/bootstrap-vue-next.css'
import '@fortawesome/fontawesome-free/css/all.css';
import '@fortawesome/fontawesome-free/js/all.js';

import './styles.css';

import { library } from '@fortawesome/fontawesome-svg-core';

import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

import { faPhone, faEnvelope, faCalendarPlus, faArrowUp , faHouse, faQuoteRight } from '@fortawesome/free-solid-svg-icons';

// Add icons to the library
library.add(faPhone, faEnvelope, faCalendarPlus, faArrowUp, faHouse, faQuoteRight  );

const app = createApp(App);

app.component('font-awesome-icon', FontAwesomeIcon);
app.use(router);
app.mount('#app');
app.use(BootstrapVueNext);