<template>
    <section class="section_gallery">
      <div class="gallery_container">
        <h2 class="gallery_title">Nasza Przychodnia</h2>
        <Splide :options="mainOptions" ref="main" class="main_slider">
          <SplideSlide v-for="(slide, index) in slides" :key="index" class="main_slide">
            <img :src="slide.src" :alt="slide.alt" class="main_image"/>
          </SplideSlide>
        </Splide>
        <Splide :options="thumbsOptions" ref="thumbs" class="thumbs_slider">
          <SplideSlide v-for="(slide, index) in slides" :key="index" class="thumb_slide">
            <img :src="slide.src" :alt="slide.alt" class="thumb_image"/>
          </SplideSlide>
        </Splide>
      </div>
    </section>
  </template>
  
  <script>
  import { ref, onMounted } from 'vue';
  import { Splide, SplideSlide } from '@splidejs/vue-splide';
  import '@splidejs/splide/dist/css/themes/splide-default.min.css';
  import '@splidejs/vue-splide/css/skyblue';
  
  export default {
    name: 'Galeria',
    components: {
      Splide,
      SplideSlide
    },
    setup() {
      const mainOptions = ref({
        type: 'fade',
        heightRatio: 0.5,
        pagination: false,
        arrows: false,
        cover: true,
        autoplay: true,
        interval: 3000,
        speed: 800,
      });
  
      const thumbsOptions = ref({
        fixedWidth: 100,
        fixedHeight: 64,
        isNavigation: true,
        gap: 10,
        focus: 'center',
        pagination: true,
        cover: true,
        arrows: true,
        breakpoints: {
          600: {
            fixedWidth: 66,
            fixedHeight: 40,
          },
        },
      });
  
      const slides = ref([
      { src: 'img/galeria/4/3.JPG', alt: 'Opis obrazu 1' },
        { src: 'img/galeria/4/7.JPG', alt: 'Opis obrazu 2' },
        { src: 'img/galeria/4/8.JPG', alt: 'Opis obrazu 3' },
        { src: 'img/galeria/1/1.JPG', alt: 'Opis obrazu 1' },
        { src: 'img/galeria/1/2.JPG', alt: 'Opis obrazu 2' },
        { src: 'img/galeria/1/3.JPG', alt: 'Opis obrazu 3' },
        { src: 'img/galeria/1/4.JPG', alt: 'Opis obrazu 5' },
        { src: 'img/galeria/2/1.JPG', alt: 'Opis obrazu 1' },
        { src: 'img/galeria/2/2.JPG', alt: 'Opis obrazu 2' },
        { src: 'img/galeria/2/3.JPG', alt: 'Opis obrazu 3' },
        { src: 'img/galeria/2/4.JPG', alt: 'Opis obrazu 4' },
        { src: 'img/galeria/2/5.JPG', alt: 'Opis obrazu 5' },
        { src: 'img/galeria/2/6.JPG', alt: 'Opis obrazu 5' },
        { src: 'img/galeria/3/1.JPG', alt: 'Opis obrazu 1' },
        { src: 'img/galeria/3/2.JPG', alt: 'Opis obrazu 2' },
        { src: 'img/galeria/3/3.JPG', alt: 'Opis obrazu 3' },
        { src: 'img/galeria/3/5.JPG', alt: 'Opis obrazu 5' }
      
      ]);
  
      const main = ref(null);
      const thumbs = ref(null);
  
      onMounted(() => {
        if (main.value && thumbs.value) {
          main.value.sync(thumbs.value.splide);
        }
      });
  
      return {
        mainOptions,
        thumbsOptions,
        slides,
        main,
        thumbs,
      };
    }
  };
  </script>
  
  <style scoped>
  .section_gallery {
    padding: 40px 0;
    background-color: #f9f9f9;
  }
  .gallery_container {
    max-width: 1300px;
    margin: 0 auto;
    text-align: center;
  }
  .gallery_title {
    font-size: 2em;
    margin-bottom: 20px;
    font-weight: bold;
  }
  .main_slider {
    margin-bottom: 20px;
  }
  .main_image {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  .thumbs_slider {
    .splide__slide {
      opacity: 0.5;
      transition: opacity 0.3s;
    }
    .splide__slide.is-active {
      opacity: 1;
    }
  }
  .thumb_image {
    width: 100%;
    height: auto;
    cursor: pointer;
    border-radius: 4px;
  }
  </style>
  