<template>
  <div id="app">
    <div class="placeholder" id="placeholder" ref="placeholder" style="height: 150px;"></div>
    <Header ref="topNavigation"/>
    
    <router-view />
    
  </div>
</template>

<script>
// Importowanie komponentów
import Header from './components/Header.vue'; // Ścieżka do twojego nagłówka

export default {
  name: 'App',
  components: {
    Header
  },
  mounted() {
    // Wywołaj funkcję do ustawienia wysokości placeholder po zamontowaniu
    this.setPlaceholderHeight();

    // Dodaj nasłuchiwanie zmiany trasy za pomocą Vue Router
    this.$router.afterEach(() => {
      this.setPlaceholderHeight();
    });
  },
  methods: {
    setPlaceholderHeight() {
      // Ustaw wysokość elementu placeholder na wysokość sekcji topNavigation
      var topNavigationHeight = this.$refs.topNavigation.offsetHeight;
      this.$refs.placeholder.style.height = topNavigationHeight + 'px';
    }
  }
}
</script>

<style>
/* Styl dla całej aplikacji */
#app {
  font-family: 'Montserrat', sans-serif;

  .router-link {
    color: #fff; 
}

a.router-link-active {
    color: #bd38a4; 
}

}
</style>