.responsive-title {
  text-align: center;
  background-color: #49c1a4;
}

h1 {
  font-size: 2em; /* Ustaw rozmiar czcionki na odpowiednią wartość */
  margin: 0;
  padding: 20px 0px;
  color: #fff; /* Dodaj kolor dla pierwszego słowa */
}

.fade-in {
  /* Dodaj tutaj animację fade-in, na przykład: */
